@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
@font-face {
  font-family: 'Cormorant Garamond';
  src: url(/solicitor-website/static/media/CormorantGaramond-Regular.22354f56.ttf) format('truetype');
  src: url(/solicitor-website/static/media/CormorantGaramond-Bold.6c964387.ttf) format('truetype');
  /* Add more src declarations for other font formats (e.g., .woff, .woff2) */
}

/* Variables */
:root {
  --primary-color: #687693;
  --secondary-color: #e3b576;
  --secondary-color-10: #ecddc7;
  --header-color: #434067;
  --light-color: #e9ebf1;
  --font-weight-bold: bold;
  --font-weight-regular: normal;
}
body {
  font-family: 'Open Sans', sans-serif;
  font-size: normal;
  color: #687693;
  color: var(--primary-color);
}
h1, h2, h3, h4, h5, h6 {
  font-family: 'Cormorant Garamond', sans-serif;
  color: #434067;
  color: var(--header-color);
  font-weight: bold;
  font-weight: var(--font-weight-bold);
}
/* Global styles */
.section-padding {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.section-title {
  padding: 6rem 1rem;
  background-color: #e9ebf1;
  background-color: var(--light-color);
  color: #687693;
  color: var(--primary-color);
  margin-bottom: 2rem;
  text-align: center;
}
.section-bg-light {
  background-color: #e9ebf1;
  background-color: var(--light-color);
}
.navbar-brand {
  width: 80px;
}
.navbar-brand img {
  width: 100%;;
}
.features-section .feature-item {
    background: #fff;
    box-shadow: 0 5px 15px 0 rgba(62,65,159,.1);
    padding: 3rem;
    margin-bottom: 3rem;
}
.feature-item .bi {
  color: #e3b576;
  color: var(--secondary-color);
  font-size: 3rem;
  display: block;
  margin-bottom: 2rem;
}
.blog-content {
  margin-bottom: 4rem;
  border-bottom: 1px solid #ecddc7;
  border-bottom: 1px solid var(--secondary-color-10);
  padding-bottom: 2rem;
}
.blog-post .blog-title {
  margin: 1rem auto;
}
.cards .card {
  margin: 1rem auto 1rem auto;
}
.carousel-caption h3 {
  color: #e9ebf1;
  color: var(--light-color);
}
.carousel-caption {

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

